@import url("https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&family=Iceberg&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --maincolor: green;
}
.btn:hover {
  cursor: pointer;
  background-color: var(--maincolor);
  box-shadow: 1px 1px 6px var(--maincolor);
  transition: 0.5s ease-out;
  color: white;
}

.navbar {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  height: 10vh;
}
.bright .navbar {
  /* background-color: #005; */
  box-shadow: 1px 1px 10px var(--maincolor);
}
.dark .navbar {
  position: relative;
  z-index: 5;
  background-color: #222831;
  box-shadow: 1px 1px 6px white;
}
.navbar ul {
  display: flex;
  align-items: center;
  gap: 50px;
  margin-left: 35px;
}
.navbar ul li {
  list-style: none;
  font-weight: bold;
  text-decoration: none;
  color: green;
}
.dark .navbar ul li {
  color: var(--maincolor);
}
.navbar ul li:hover {
  cursor: pointer;
}
.navbar .searchbar {
  display: flex;
  align-items: center;
  gap: 25px;
  height: 100%;
}
.searchinput {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
  border: 1px solid black;
  padding: 2px;
  border-radius: 8px;
  width: 18vw;
}
.dark .searchinput {
  background-color: white;
}
.searchinput img {
  height: 20px;
  width: 20px;
}
.navbar button {
  height: 50%;
  padding: 0 10px 0 10px;
  border-radius: 7px;
  border: 1px solid black;
  background-color: white;
}
.navbar input {
  height: 100%;
  border: none;
  outline: none;
  margin-left: 5px;
  width: 100%;
}
.modeIcon {
  display: flex;
  align-items: center;
}
.navbar .modeIcon img {
  height: 40px;
  width: 40px;
  margin-right: 25px;
}
.navbar .modeIcon img:hover {
  cursor: pointer;
}
.list {
  text-decoration: none;
}

@media (max-width: 480px) {
  .navbar {
    width: 100vw;
    height: 8vh;
  }
  .searchbar {
    display: none !important;
  }
  .list {
    font-size: small;
  }
  .navbar .modeIcon img {
    height: 35px;
    width: 35px;
  }
}

/* ************************* Foooter *****************************************  */
.footer {
  height: 7.5vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  position: relative;
  z-index: 5;
}
.bright .footer {
  box-shadow: inset 1px 1px 36px var(--maincolor);
}
.dark .footer {
  background-color: #222831;
  color: white;
}

/* **************************** About **************************** */

.aboutContainer {
  height: 82.5vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 5px;
}
.heading {
  margin-top: 5px;
  font-size: x-large;
  font-family: iceberg;
}
.dark .aboutContainer {
  background-color: #31363f;
  color: white;
}
.dark .heading {
  background-color: #31363f;
}

.aboutContainer .subAboutContainer {
  position: relative;
  z-index: 4;
  box-shadow: 1px 1px 4px var(--maincolor);
  padding: 20px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 40vw;
  overflow: hidden;
}

.subAboutContainer p {
  overflow: hidden;
  height: 100%;
  font-family: iceberg;
  margin-top: 10px;
  font-size: large;
}
.introduction {
  display: flex;
  justify-content: space-around;
}
.information {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
.introduction,
.information {
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.introduction .information {
  overflow: hidden;
}
.subAboutContainer img {
  height: 10px;
  filter: drop-shadow(1px 1px 1px var(--maincolor));
}
.aboutBox {
  display: flex;
  justify-content: space-around;
  gap: 50px;
}
.aboutBox .aboutImg {
  margin-top: -50px;
}
.subAboutBoxes {
  margin-top: 10vh;
}
.introduction h2 {
  font-family: goldman;
}
@media (max-width: 480px) {
  .aboutImg {
    display: none;
  }
  .footer {
    /* display: none; */
    margin-top: 5px !important;
  }
  .subAboutBoxes {
    margin-top: 25px;
  }
  .subAboutContainer {
    width: 90vw !important;
  }
  .introduction p {
    font-size: large !important;
    overflow-y: scroll;
  }
}

/* ******************************************** Home ********************************************************* */

.homeContainer {
  height: 70vh;
  display: flex;
  align-items: center;
}
.mobBtns {
  display: none;
}
.dark .homeContainer {
  background-color: #31363f;
}
.footer {
  position: relative;
}
.heading {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: x-large;
  margin-bottom: 10px;
  margin-top: 0;
}
.dark .heading {
  color: rgba(255, 255, 255, 0.781);
}
.heading h1 {
  margin-top: 10px;
}
.heading h1 span {
  color: green;
}
.homeContainer .btns {
  margin-top: 25px;
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 10px;
}
.homeContainer .btns button {
  background-color: green;
  color: white;
  width: 150px;
  height: 50px;
  border: none;
  outline: linen;
  font-size: large;
  border-radius: 8px;
  box-shadow: 1px 1px 8px green;
  border: 1px solid rgba(255, 255, 255, 0.589);
}
.homeContainer .btns button:hover {
  cursor: pointer;
  transition: 0.2s linear;
  box-shadow: 1px 1px 6px black;
}
.dark .homeContainer .btns button:hover {
  box-shadow: 1px 1px 12px white;
}
canvas {
  border: 3px solid black;
}
.dark canvas {
  border: 3px solid white;
}
.canvas {
  margin-left: 10px;
  margin-bottom: 10px;
}
.scores {
  margin-top: -25px;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 100px;
  font-size: larger;
  font-weight: bold;
}
.dark .scores {
  color: white;
}
.scores p {
  margin-left: 30px;
}
.scores span {
  color: red;
}
.dark .scores {
  background-color: #31363f;
}
.gamebtns div:first-child {
  margin-left: 20px;
}
.gamebtns span {
  margin-left: 0px;
  font-weight: bold;
  color: red;
  font-size: larger;
}
.mobAlert {
  display: none;
}
@media (max-width: 480px) {
  .homeContainer {
    height: auto;
    overflow-x: hidden;
  }
  .mobBtns img {
    height: 40px;
    width: 40px;
    background-color: white !important;
    border-radius: 50%;
  }

  .mobBtns .lr button:first-child {
    position: absolute;
    left: -40px;
    top: 60%;
    transform: translateY(-50%) rotate(180deg);
    margin-top: 60px;
  }

  .mobBtns .lr button:last-child {
    position: absolute;
    right: -40px;
    transform: translateY(-50%);
    margin-top: 60px;
  }

  .mobBtns .tb {
    position: relative;
    z-index: 2;
  }
  .mobBtns .lr {
    position: relative;
  }

  .mobBtns .tb button:first-child {
    position: absolute;
    left: 50%;
    top: 80px;
    transform: translateX(-50%) rotate(90deg);
  }

  .mobBtns .tb button:last-child {
    position: absolute;
    left: 50%;
    bottom: -50px;
    transform: translateX(-50%) rotate(-90deg);
  }

  .mobBtns {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }

  .btns {
    display: flex;
    flex-direction: row !important;
    align-items: center;
    width: 100vw;
    justify-content: space-around !important;
    margin-top: 5px !important;
    height: 5vh !important;
  }
  .scores p {
    margin-top: 15px;
  }
  .heading {
    text-align: center;
  }

  .scores span {
    margin-left: 40px;
  }
  .homeContainer .btns button {
    width: 90px;
    height: 30px !important;
    font-size: small;
  }
  canvas {
    width: 90vw;
    margin-top: 90px;
  }
  .homeContainer {
    display: flex;
    flex-direction: column;
  }

  .alert {
    display: none;
  }
  .mobAlert {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
    color: red;
    font-size: large;
    font-weight: bold;
    margin-bottom: -70px;
  }
}

/* ****************************************************** Contact ************************************************ */
section {
  height: 82.5vh;
  /* padding: 10rem 7% 2rem; */
}
.dark .contact {
  background-color: #31363f;
}
.contact .heading {
  font-size: 50px;
  font-family: iceberg;
}
.contact .heading span {
  color: green;
  margin-left: 15px;
}
.contact #msg {
  font-size: 1.3rem;
}
.contact form {
  max-width: 60vw;
  margin: 1rem auto;
  text-align: center;
  /* margin-bottom: 3rem; */
}

.contact form .input-box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact form .input-box input,
.contact form textarea {
  width: 100%;
  padding: 1.5rem;
  font-size: 1.2rem;
  background: var(--bg-color);
  border-radius: 0.8rem;
  margin: 0.7rem 0;
  box-shadow: 0 0.1rem 0.5rem green;
  border: none;
  outline: none;
}

.contact form .input-box input {
  width: 49%;
  height: 2.5vh;
}

.contact form textarea {
  resize: none;
  height: 30vh;
  font-size: 1.2rem;
}

.contact form .btn {
  /* margin-top: 2rem; */
  cursor: pointer;
}
.dark-mode .contact form .input-box input {
  box-shadow: 2px 2px 6px white;
  opacity: 0.7;
}
.dark-mode .contact form textarea {
  box-shadow: 2px 2px 6px white;
  opacity: 0.7;
}

.subBtn {
  height: 50px;
  width: 150px;
  padding: 10px;
  border-radius: 8px;
  border: none;
  outline: none;
  font-size: 1.1rem;
  margin-top: 8px;
}
.subBtn:hover {
  cursor: pointer;
  background-color: green !important;
  transition: 0.3s linear;
  color: white !important;
}
.bright .subBtn {
  background-color: white;
  color: black;
  box-shadow: inset 0px 0px 28px green;
}
.dark .subBtn {
  background-color: #31363f;
  color: gray;
  box-shadow: 1px 1px 8px green;
}
@media (max-width: 480px) {
  section {
    height: auto;
  }
  .contact form {
    width: 100vw;
    margin: 0;
  }
.mobtxtarea{
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
}
.mobtxtarea #msg{
  width: 75%;
}
  .contact .input-box {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .input-box input{
    width: 75vw !important;
  }
  .footer{
   margin-top: 20px;
  }
}
